module.exports = "import { Component, ViewEncapsulation } from '@angular/core';\n\n@Component({\n  selector     : 'nz-demo-mention-basic',\n  encapsulation: ViewEncapsulation.None,\n  template     : `\n  <nz-mention\n    [nzSuggestions]=\"suggestions\"\n    (nzOnSelect)=\"onSelect($event)\">\n    <input\n      placeholder=\"input here\"\n      nzMentionTrigger\n      nz-input\n      [(ngModel)]=\"inputValue\"\n      (ngModelChange)=\"onChange($event)\"\n    >\n  </nz-mention>\n`\n})\nexport class NzDemoMentionBasicComponent {\n  inputValue: string = '@afc163';\n  suggestions = ['afc163', 'benjycui', 'yiminghe', 'RaoHai', '中文', 'にほんご'];\n\n  onChange(value: string): void {\n    console.log(value);\n  }\n\n  onSelect(suggestion: string): void {\n    console.log(`onSelect ${suggestion}`);\n  }\n}\n"